   content: '';\n        display: block;\n        height: 64px;\n    }\n}\n\n.section-heading {\n    font-size: 18px;\n    margin: 0 0 20px 0;\n}\n\n.page_header {\n    border-bottom: 1px solid #f0f0f0;\n\n    .logo-and-nav {\n        padding: 22px 0;\n        display: flex;\n        align-items: center;\n        flex-wrap: wrap;\n    }\n\n    .logo {\n        a {\n            display: block;\n            padding: 2px 0 5px 0;\n        }\n    }\n\n    .menu-btn {\n        width: 30px;\n        height: 30px;\n        background: url(../img/icon_list.svg) no-repeat center;\n        border: none;\n        margin-left: auto;\n        display: none;\n    }\n\n    .menu {\n        display: flex;\n        list-style: none;\n\n        a {\n            font-size: 16px;\n            color: @main-font-color;\n            line-height: 20px;\n            text-decoration: none;\n            font-weight: 700;\n            opacity: .6;\n            padding: 5px;\n            margin: 0 10px;\n            transition: opacity .2s ease-in-out;\n\n            &:not(:last-child) {\n                margin-right: 8px;\n            }\n\n            &:hover {\n                opacity: 1;\n            }\n        }\n\n        .active {\n            opacity: 1;\n        }\n    }\n}\n\n/* Плашка для сценариев и устройств */\n.billet {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-end;\n    width: 200px;\n    height: 120px;\n    background: @main-button-color;\n    border-radius: 20px;\n    padding: 14px;\n    box-sizing: border-box;\n    position: relative;\n    background-position: 14px 14px;\n    background-repeat: no-repeat;\n    text-align: left;\n\n    .name {\n        font-weight: 700;\n        margin: 0;\n        margin-bottom: 4px;\n    }\n\n    .action {\n        font-size: 12px;\n        color: #858585;\n        margin: 0;\n    }\n\n    &.icon_sun {\n        background-image: url(../img/icon_sun.svg);\n    }\n    \n    &.icon_sun_2 {\n        background-image: url(../img/icon_sun_2.svg);\n    }\n    \n    &.icon_temperature {\n        background-image: url(../img/icon_temperature.svg);\n    }\n    \n    &.icon_temperature_2 {\n        background-image: url(../img/icon_temperature_2.svg);\n    }\n    \n    &.icon_scheduled {\n        background-image: url(../img/icon_scheduled.svg);\n    }\n}\n\n.main-and-scenarios {\n    padding: 29px 0;\n    display: flex;\n    justify-content: space-between;\n\n    .section-main,\n    .section-scenarios {\n        width: 49%;\n    }\n\n    .inner-block {\n        width: 100%;\n        height: 331px;\n        border-radius: 20px;\n        display: flex;\n        justify-content: space-between;\n    }\n}\n    \n.section-main {\n    .inner-block {\n        background-image: url(../img/bg@1x.png);\n        background-position: center;\n        background-size: cover;\n        background-repeat: no-repeat;\n        padding: 25px 20px 25px 25px;\n        box-sizing: border-box;\n    }\n\n    .greeting {\n        font-size: 27px;\n        font-weight: 700;\n        margin: 0;\n    }\n\n    .status-text {\n        margin: 6px 0 0;\n    }\n\n    .temperature-blocks {\n        margin-top: 25px;\n\n        &:after {\n            content: '';\n            display: inline-block;\n            width: 26px;\n            height: 26px;\n            background-image: url(../img/cloud-drizzle.svg);\n            margin-left: 5px;\n        }\n\n        .block {\n            display: inline-block;\n            &:not(:last-child) {\n                margin-right: 15px;\n            }\n\n            .label {\n                color: #858585;\n                margin: 0;\n            }\n\n            .temperature {\n                font-size: 36px;\n                font-weight: 700;\n                margin: 4px 0 0;\n            }\n        }\n    }\n\n    .devices-slider {\n        margin: -25px 0;\n        height: 331px;\n        width: 200px;\n        min-width: 200px;\n        overflow: hidden;\n        position: relative;\n    }\n\n    .devices {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n        overflow-y: scroll;\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        left: 0;\n\n        li {\n            margin: 15px 0;\n\n            &:first-child {\n                margin-top: 20px;\n            }\n\n            &:last-child {\n                margin-bottom: 20px;\n            }\n\n            &:nth-child(3) {\n                .billet:after {\n                    content: '';\n                    position: absolute;\n                    width: 16px;\n                    height: 16px;\n                    background-image: url(../img/double-arrow-up.svg);\n                    left: 50%;\n                    margin-left: -8px;\n                    top: 13px;\n                    transition: opacity .2s ease-in-out;\n                }\n            }\n        }\n\n        &.scrolled li:nth-child(3) .billet:after {\n            opacity: 0;\n        }\n    }\n}\n\n.section-scenarios {\n    .scenarios-slider {\n        overflow: hidden;\n        padding: 2px;\n        margin: -4px -23px -8px -2px;\n    }\n\n    .heading-and-controls {\n        display: flex;\n\n        .controls {\n            margin-left: auto;\n\n            button {\n                background: none;\n                border: none;\n                opacity: .3;\n                padding: 5px 10px;\n            }  \n        }\n    }\n\n    .scenarios {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: flex-start;\n\n        button {\n            border: none;\n            margin: 0;\n            padding: 0;\n            background: none;\n            margin: 2px 15px 15px 2px;\n\n            .billet {\n                width: 200px;\n                height: 100px;\n            }\n\n            &:focus {\n                outline: none;\n\n                .billet {\n 